<template>
  <view class="demo-page">
    <view class="header">
      <text class="page-title"></text>
    </view>
    
    <!--  -->
    <view class="demo-card">
      <view class="card-header">
        <text class="card-title"></text>
        <text class="card-desc">3</text>
      </view>
      <scroll-banner 
        :images="bannerImages"
        direction="horizontal"
        :interval="3000"
        :duration="300"
        height="180rpx"
        :auto-play="true"
        :draggable="true"
        :show-indicators="true"
        @change="onBannerChange"
        @click="onBannerClick"
      />
    </view>
    
    <!--  -->
    <view class="demo-card">
      <view class="card-header">
        <text class="card-title"></text>
        <text class="card-desc">1.5</text>
      </view>
      <scroll-banner 
        :images="newsImages"
        direction="horizontal"
        :interval="1500"
        :duration="200"
        height="120rpx"
        :auto-play="true"
        :draggable="true"
        :show-indicators="false"
      />
    </view>
    
    <!--  -->
    <view class="demo-card">
      <view class="card-header">
        <text class="card-title"></text>
        <text class="card-desc"></text>
      </view>
      <scroll-banner 
        :images="productImages"
        direction="vertical"
        :interval="4000"
        :duration="400"
        height="300rpx"
        :auto-play="true"
        :draggable="true"
        :show-indicators="true"
      />
    </view>
    
    <!--  -->
    <view class="demo-card">
      <view class="card-header">
        <text class="card-title"></text>
        <text class="card-desc"></text>
      </view>
      <scroll-banner 
        :images="fullscreenImages"
        direction="horizontal"
        :interval="5000"
        :duration="500"
        height="250rpx"
        :auto-play="true"
        :draggable="true"
        :show-indicators="true"
      />
    </view>
    
    <!--  -->
    <view class="control-panel">
      <text class="panel-title"></text>
      <view class="control-group">
        <view class="control-item">
          <text class="control-label">: {{ currentInterval }}ms</text>
          <slider 
            :value="currentInterval" 
            :min="1000" 
            :max="8000" 
            @change="onIntervalChange"
            activeColor="#007aff"
          />
        </view>
        <view class="control-buttons">
          <button @click="toggleDirection" class="control-btn">
            {{ currentDirection === 'horizontal' ? '' : '' }}
          </button>
          <button @click="toggleAutoPlay" class="control-btn">
            {{ autoPlay ? '' : '' }}
          </button>
        </view>
      </view>
    </view>
    
    <!--  -->
    <view class="demo-card">
      <view class="card-header">
        <text class="card-title"></text>
        <text class="card-desc"></text>
      </view>
      <scroll-banner 
        :images="controlledImages"
        :direction="currentDirection"
        :interval="currentInterval"
        :duration="300"
        :height="currentDirection === 'vertical' ? '250rpx' : '180rpx'"
        :auto-play="autoPlay"
        :show-indicators="true"
      />
    </view>
  </view>
</template>

<script>
import ScrollBanner from '@/components/scroll-banner.vue'

export default {
  components: {
    ScrollBanner
  },
  data() {
    return {
      currentInterval: 3000,
      currentDirection: 'horizontal',
      autoPlay: true,
      
      // 
      bannerImages: [
        { src: 'https://picsum.photos/800/300?random=1&text=11', alt: '11' },
        { src: 'https://picsum.photos/800/300?random=2&text=618', alt: '618' },
        { src: 'https://picsum.photos/800/300?random=3&text=', alt: '' }
      ],
      
      // 
      productImages: [
        { src: 'https://picsum.photos/400/200?random=6', alt: '' },
        { src: 'https://picsum.photos/400/200?random=7', alt: '' },
        { src: 'https://picsum.photos/400/200?random=8', alt: '' },
        { src: 'https://picsum.photos/400/200?random=9', alt: '' },
        { src: 'https://picsum.photos/400/200?random=10', alt: '' },
        { src: 'https://picsum.photos/400/200?random=11', alt: '' }
      ],
      
      // 
      newsImages: [
        { src: 'https://picsum.photos/600/150?random=12', alt: '1' },
        { src: 'https://picsum.photos/600/150?random=13', alt: '2' },
        { src: 'https://picsum.photos/600/150?random=14', alt: '3' },
        { src: 'https://picsum.photos/600/150?random=15', alt: '4' }
      ],
      
      // 
      fullscreenImages: [
        { src: 'https://picsum.photos/800/400?random=16&text=11', alt: '11' },
        { src: 'https://picsum.photos/800/400?random=17&text=618', alt: '618' },
        { src: 'https://picsum.photos/800/400?random=18&text=', alt: '' }
      ],
      
      // 
      controlledImages: [
        { src: 'https://picsum.photos/700/250?random=19', alt: '1' },
        { src: 'https://picsum.photos/700/250?random=20', alt: '2' },
        { src: 'https://picsum.photos/700/250?random=21', alt: '3' },
        { src: 'https://picsum.photos/700/250?random=22', alt: '4' }
      ]
    }
  },
  methods: {
    onIntervalChange(e) {
      this.currentInterval = e.detail.value
    },
    
    toggleDirection() {
      this.currentDirection = this.currentDirection === 'horizontal' ? 'vertical' : 'horizontal'
    },
    
    toggleAutoPlay() {
      this.autoPlay = !this.autoPlay
    },
    
    onBannerChange(e) {
      console.log(':', e)
    },
    
    onBannerClick(e) {
      console.log(':', e)
      uni.showToast({
        title: `: ${e.item.alt}`,
        icon: 'none'
      })
    }
  }
}
</script>

<style scoped>
.demo-page {
  background-color: #f5f7fa;
  min-height: 100vh;
  padding: 20rpx;
}

.header {
  text-align: center;
  padding: 30rpx 0;
  margin-bottom: 20rpx;
}

.page-title {
  font-size: 44rpx;
  font-weight: bold;
  color: #2c3e50;
}

.demo-card {
  background-color: #ffffff;
  border-radius: 16rpx;
  margin-bottom: 30rpx;
  padding: 24rpx;
  box-shadow: 0 2rpx 12rpx rgba(0, 0, 0, 0.08);
}

.card-header {
  margin-bottom: 20rpx;
}

.card-title {
  font-size: 32rpx;
  font-weight: 600;
  color: #2c3e50;
  display: block;
  margin-bottom: 8rpx;
}

.card-desc {
  font-size: 24rpx;
  color: #7f8c8d;
  display: block;
}

.control-panel {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 16rpx;
  padding: 30rpx;
  margin-bottom: 30rpx;
  color: white;
}

.panel-title {
  font-size: 36rpx;
  font-weight: bold;
  display: block;
  margin-bottom: 30rpx;
  text-align: center;
}

.control-group {
  width: 100%;
}

.control-item {
  margin-bottom: 30rpx;
}

.control-label {
  font-size: 28rpx;
  display: block;
  margin-bottom: 15rpx;
  opacity: 0.9;
}

.control-buttons {
  display: flex;
  gap: 20rpx;
  flex-wrap: wrap;
}

.control-btn {
  flex: 1;
  min-width: 200rpx;
  background-color: rgba(255, 255, 255, 0.2);
  color: white;
  border: 1rpx solid rgba(255, 255, 255, 0.3);
  border-radius: 12rpx;
  padding: 20rpx;
  font-size: 26rpx;
  backdrop-filter: blur(10rpx);
}

.control-btn:active {
  background-color: rgba(255, 255, 255, 0.3);
}

/*  */
slider {
  width: 100%;
}
</style>
