// TabBar
export const TabBarManager = {
  // TabBar
  hideNativeTabBar() {
    try {
      uni.hideTabBar({
        animation: false,
        success: () => {
          console.log('TabBar')
        },
        fail: (err) => {
          console.log('TabBar:', err)
        }
      })
    } catch (e) {
      console.log('hideTabBar:', e)
    }
  },

  // TabBarApp.vue
  initTabBar() {
    // TabBar
    setTimeout(() => {
      this.hideNativeTabBar()
    }, 100)
  },

  // TabBar
  onPageSwitch() {
    // TabBar
    this.hideNativeTabBar()
  }
}

export default TabBarManager
