<template>
    <view class="city-selector-page">
        <!--  -->
        <view class="nav-bar" :style="{ paddingTop: statusBarHeight + 30 + 'px' }">
            <text class="nav-title"></text>
            <view class="nav-placeholder"></view>
        </view>

        <!--  -->
        <view class="search-container">
            <view class="search-box">
                <view class="search-icon">
                    <view class="search-icon-svg">
                        <view class="search-circle"></view>
                        <view class="search-handle"></view>
                    </view>
                </view>
                <input class="search-input" placeholder="" v-model="searchKeyword" @input="onSearchInput"
                    :focus="searchFocus" />
                <view v-if="searchKeyword" class="clear-btn" @tap="clearSearch">
                    <text class="clear-icon"></text>
                </view>
            </view>
        </view>

        <!--  -->
        <view v-if="!searchKeyword" class="hot-cities">
            <text class="section-title"></text>
            <view class="hot-city-grid">
                <view v-for="city in hotCities" :key="city.code" class="hot-city-item" @tap="selectCity(city)">
                    <text class="hot-city-name">{{ city.name.replace('', '') }}</text>
                </view>
            </view>
        </view>

        <!--  -->
        <view class="city-list-container">
            <!--  -->
            <view v-if="searchKeyword" class="search-results">
                <view v-if="searchResults.length === 0" class="no-results">
                    <text class="no-results-text"></text>
                </view>
                <view v-else class="search-result-list">
                    <view v-for="city in searchResults" :key="city.code" class="city-item search-result-item"
                        @tap="selectCity(city)">
                        <text class="city-name">{{ city.name }}</text>
                    </view>
                </view>
            </view>

            <!--  -->
            <scroll-view v-else class="city-list" scroll-y :scroll-into-view="scrollIntoView"
                :scroll-with-animation="false" @scroll="onScroll">
                <view v-for="letter in alphabetList" :key="letter" :id="`letter-${letter}`" class="letter-section">
                    <view class="letter-header">
                        <text class="letter-title">{{ letter }}</text>
                    </view>
                    <view class="city-group">
                        <view v-for="city in groupedCities[letter]" :key="city.code" class="city-item"
                            @tap="selectCity(city)">
                            <text class="city-name">{{ city.name }}</text>
                        </view>
                    </view>
                </view>
            </scroll-view>

            <!--  -->
            <view v-if="!searchKeyword" class="alphabet-index" @touchstart="onIndexTouchStart"
                @touchmove="onIndexTouchMove" @touchend="onIndexTouchEnd">
                <view v-for="letter in alphabetList" :key="letter" :data-letter="letter" class="index-letter"
                    :class="{ active: currentLetter === letter }" @tap="onLetterTap(letter)">
                    <text class="index-letter-text">{{ letter }}</text>
                </view>
            </view>

            <!--  -->
            <view v-if="showLetterTip && !searchKeyword" class="letter-tip">
                <text class="letter-tip-text">{{ currentLetter }}</text>
            </view>
        </view>
    </view>
</template>

<script>
import { citiesData } from '@/utils/cities-data.js'

export default {
    name: 'CitySelector',
    data() {
        return {
            searchKeyword: '',
            searchFocus: false,
            currentCity: '',
            cities: [],
            groupedCities: {},
            alphabetList: [],
            hotCities: [],
            searchResults: [],
            scrollIntoView: '',
            currentLetter: '',
            showLetterTip: false,
            indexTouching: false,
            statusBarHeight: 44, // 
            headerHeight: 44     // 
        }
    },
    async onLoad(options) {
        // 
        if (options.city) {
            this.currentCity = decodeURIComponent(options.city)
        }

        // 
        this.getSystemInfo()

        await this.loadCities()
        this.setupHotCities()
        this.groupCitiesByLetter()
    },
    methods: {
        async loadCities() {
            // 
            this.cities = citiesData || []
            console.log('', this.cities.length, '')
        },

        setupHotCities() {
            // 
            const hotCityCodes = ['1101', '3101', '4403', '4401', '3301', '3201', '5001', '5101']
            this.hotCities = this.cities.filter(city => hotCityCodes.includes(city.code))
        },

        groupCitiesByLetter() {
            const grouped = {}
            const alphabet = []

            // 
            this.cities.forEach(city => {
                const firstChar = this.getFirstLetter(city.name)
                if (!grouped[firstChar]) {
                    grouped[firstChar] = []
                    if (!alphabet.includes(firstChar)) {
                        alphabet.push(firstChar)
                    }
                }
                grouped[firstChar].push(city)
            })

            // 
            alphabet.sort()

            // 
            Object.keys(grouped).forEach(letter => {
                grouped[letter].sort((a, b) => a.name.localeCompare(b.name, 'zh-CN'))
            })

            this.groupedCities = grouped
            this.alphabetList = alphabet

            console.log(':', Object.keys(grouped).length, '')
        },

        getFirstLetter(cityName) {
            // 
            const letterMap = {
                // A
                '': 'A', '': 'A', '': 'A',
                // B
                '': 'B', '': 'B', '': 'B', '': 'B', '': 'B', '': 'B', '': 'B', '': 'B', '': 'B', '': 'B', '': 'B', '': 'B',
                // C
                '': 'C', '': 'C', '': 'C', '': 'C', '': 'C', '': 'C', '': 'C', '': 'C', '': 'C', '': 'C', '': 'C', '': 'C', '': 'C', '': 'C', '': 'C',
                // D
                '': 'D', '': 'D', '': 'D', '': 'D', '': 'D', '': 'D', '': 'D',
                // E
                '': 'E', '': 'E',
                // F
                '': 'F', '': 'F', '': 'F', '': 'F', '': 'F',
                // G
                '': 'G', '': 'G', '': 'G', '': 'G', '': 'G', '': 'G', '': 'G',
                // H
                '': 'H', '': 'H', '': 'H', '': 'H', '': 'H', '': 'H', '': 'H', '': 'H', '': 'H', '': 'H', '': 'H', '': 'H', '': 'H', '': 'H', '': 'H', '': 'H', '': 'H', '': 'H', '': 'H', '': 'H', '': 'H',
                // J
                '': 'J', '': 'J', '': 'J', '': 'J', '': 'J', '': 'J', '': 'J', '': 'J', '': 'J', '': 'J', '': 'J', '': 'J', '': 'J', '': 'J', '': 'J',
                // K
                '': 'K', '': 'K', '': 'K', '': 'K',
                // L
                '': 'L', '': 'L', '': 'L', '': 'L', '': 'L', '': 'L', '': 'L', '': 'L', '': 'L', '': 'L', '': 'L', '': 'L', '': 'L', '': 'L', '': 'L', '': 'L', '': 'L', '': 'L', '': 'L', '': 'L', '': 'L',
                // M
                '': 'M', '': 'M', '': 'M', '': 'M', '': 'M', '': 'M',
                // N
                '': 'N', '': 'N', '': 'N', '': 'N', '': 'N',
                // P
                '': 'P', '': 'P', '': 'P', '': 'P', '': 'P', '': 'P', '': 'P',
                // Q
                '': 'Q', '': 'Q', '': 'Q', '': 'Q', '': 'Q', '': 'Q', '': 'Q', '': 'Q', '': 'Q', '': 'Q', '': 'Q',
                // R
                '': 'R',
                // S
                '': 'S', '': 'S', '': 'S', '': 'S', '': 'S', '': 'S', '': 'S', '': 'S', '': 'S', '': 'S', '': 'S', '': 'S', '': 'S', '': 'S', '': 'S', '': 'S', '': 'S', '': 'S', '': 'S', '': 'S', '': 'S',
                // T
                '': 'T', '': 'T', '': 'T', '': 'T', '': 'T', '': 'T', '': 'T', '': 'T', '': 'T', '': 'T',
                // W
                '': 'W', '': 'W', '': 'W', '': 'W', '': 'W', '': 'W', '': 'W', '': 'W', '': 'W', '': 'W', '': 'W',
                // X
                '': 'X', '': 'X', '': 'X', '': 'X', '': 'X', '': 'X', '': 'X', '': 'X', '': 'X', '': 'X', '': 'X', '': 'X', '': 'X', '': 'X', '': 'X', '': 'X',
                // Y
                '': 'Y', '': 'Y', '': 'Y', '': 'Y', '': 'Y', '': 'Y', '': 'Y', '': 'Y', '': 'Y', '': 'Y', '': 'Y', '': 'Y', '': 'Y', '': 'Y', '': 'Y', '': 'Y', '': 'Y', '': 'Y',
                // Z
                '': 'Z', '': 'Z', '': 'Z', '': 'Z', '': 'Z', '': 'Z', '': 'Z', '': 'Z', '': 'Z', '': 'Z', '': 'Z', '': 'Z', '': 'Z', '': 'Z', '': 'Z', '': 'Z', '': 'Z', '': 'Z', '': 'Z', '': 'Z',
            }
            const firstChar = cityName.charAt(0)
            return letterMap[firstChar] || '#'
        },

        onSearchInput() {
            if (this.searchKeyword.trim()) {
                this.searchResults = this.cities.filter(city =>
                    city.name.includes(this.searchKeyword.trim())
                )
            } else {
                this.searchResults = []
            }
        },

        clearSearch() {
            this.searchKeyword = ''
            this.searchResults = []
        },

        selectCity(city) {
            console.log(':', city.name)

            // 
            const pages = getCurrentPages()
            const prevPage = pages[pages.length - 2]

            if (prevPage) {
                // 
                if (prevPage.$vm && typeof prevPage.$vm.onCitySelected === 'function') {
                    prevPage.$vm.onCitySelected(city)
                }
                // 
                uni.$emit('citySelected', city)
            }

            // 
            uni.navigateBack()
        },

        goBack() {
            uni.navigateBack()
        },

        // 
        onLetterTap(letter) {
            this.currentLetter = letter
            // 
            this.scrollIntoView = ''
            this.$nextTick(() => {
                this.scrollIntoView = `letter-${letter}`
            })
            this.showLetterTip = true
            setTimeout(() => {
                this.showLetterTip = false
            }, 300) // 
        },

        onIndexTouchStart(e) {
            this.indexTouching = true
            this.showLetterTip = true
            this.handleIndexTouch(e)
        },

        onIndexTouchMove(e) {
            if (this.indexTouching) {
                this.handleIndexTouch(e)
            }
        },

        onIndexTouchEnd(e) {
            this.indexTouching = false
            setTimeout(() => {
                this.showLetterTip = false
            }, 300) // 
        },

        handleIndexTouch(e) {
            const touch = e.touches[0] || e.changedTouches[0]

            if (!touch) return

            // 
            uni.createSelectorQuery().in(this).select('.alphabet-index').boundingClientRect((rect) => {
                if (rect) {
                    // clientYpageY
                    const relativeY = touch.clientY - rect.top
                    const letterHeight = rect.height / this.alphabetList.length
                    const index = Math.floor(relativeY / letterHeight)

                    if (index >= 0 && index < this.alphabetList.length) {
                        const letter = this.alphabetList[index]
                        this.currentLetter = letter

                        // 
                        this.scrollIntoView = ''
                        this.$nextTick(() => {
                            this.scrollIntoView = `letter-${letter}`
                        })
                    }
                }
            }).exec()
        },

        onScroll(e) {
            // 
            if (!this.indexTouching) {
                // 
            }
        },

        // 
        getSystemInfo() {
            try {
                const systemInfo = uni.getSystemInfoSync()
                // 
                this.statusBarHeight = systemInfo.statusBarHeight || 44
                
                // 
                if (systemInfo.platform === 'ios') {
                    // iPhone
                    if (systemInfo.statusBarHeight >= 44) {
                        this.headerHeight = 44  // iPhone X
                    } else {
                        this.headerHeight = 44  // iPhone 8
                    }
                } else if (systemInfo.platform === 'android') {
                    // Android
                    this.headerHeight = 48
                } else {
                    this.headerHeight = 44
                }
                
                // 
                const safeArea = systemInfo.safeArea || {}
                const safeAreaTop = safeArea.top || this.statusBarHeight
                
                // 
                this.statusBarHeight = Math.max(this.statusBarHeight, safeAreaTop)
                
                console.log(':', {
                    platform: systemInfo.platform,
                    statusBarHeight: this.statusBarHeight,
                    headerHeight: this.headerHeight,
                    model: systemInfo.model,
                    safeArea: safeArea
                })
            } catch (e) {
                console.log(':', e)
                // 
                const userAgent = navigator.userAgent || ''
                if (userAgent.includes('iPhone')) {
                    this.statusBarHeight = 44
                    this.headerHeight = 44
                } else {
                    this.statusBarHeight = 44
                    this.headerHeight = 48
                }
            }
        }
    }
}
</script>

<style scoped>
/* CSS */
:root {
  --status-bar-height: 44px;
}

/*  */
@supports (top: env(safe-area-inset-top)) {
  :root {
    --status-bar-height: env(safe-area-inset-top);
  }
}

/*  */
@supports (top: constant(safe-area-inset-top)) {
  :root {
    --status-bar-height: constant(safe-area-inset-top);
  }
}

.city-selector-page {
    height: 100vh;
    background-color: #f5f6f8;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    overflow-y: hidden;
}

/*  */
.nav-bar {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 20rpx 30rpx;
    background-color: white;
    position: relative;
    z-index: 100;
    /* CSS */
    padding-top: var(--status-bar-height, 44px);
}

.nav-title {
    font-size: 36rpx;
    font-weight: bold;
    color: #333;
}

.nav-placeholder {
    width: 80rpx;
}

/*  */
@media screen and (device-width: 414px) and (device-height: 896px) {
  /* iPhone XR, iPhone 11 */
  .nav-bar {
    padding-top: 44px;
  }
}

@media screen and (device-width: 375px) and (device-height: 812px) {
  /* iPhone X, iPhone XS, iPhone 11 Pro */
  .nav-bar {
    padding-top: 44px;
  }
}

@media screen and (device-width: 414px) and (device-height: 896px) {
  /* iPhone XS Max, iPhone 11 Pro Max */
  .nav-bar {
    padding-top: 44px;
  }
}

/*  */
.search-container {
    background-color: white;
    padding: 20rpx;
    border-bottom: 1rpx solid #e9ecef;
}

.search-box {
    display: flex;
    align-items: center;
    background: #f8f9fa;
    border: 1rpx solid #e9ecef;
    border-radius: 25rpx;
    padding: 0 20rpx;
}

.search-icon {
    margin-right: 15rpx;
}

.search-icon-svg {
    position: relative;
    width: 32rpx;
    height: 32rpx;
}

.search-circle {
    width: 24rpx;
    height: 24rpx;
    border: 3rpx solid #999;
    border-radius: 50%;
    position: absolute;
    top: 0;
    left: 0;
}

.search-handle {
    width: 12rpx;
    height: 3rpx;
    background-color: #999;
    border-radius: 2rpx;
    position: absolute;
    bottom: 7rpx;
    right: -5rpx;
    transform: rotate(40deg);
    transform-origin: left center;
}

.search-input {
    flex: 1;
    padding: 25rpx 0;
    font-size: 28rpx;
    border: none;
    background: transparent;
}

.clear-btn {
    width: 40rpx;
    height: 40rpx;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.clear-icon {
    font-size: 32rpx;
    color: #999;
}

/*  */
.hot-cities {
    background-color: white;
    padding: 30rpx 20rpx;
    border-bottom: 1rpx solid #e9ecef;
}

.section-title {
    font-size: 28rpx;
    color: #666;
    margin-bottom: 20rpx;
    display: block;
}

.hot-city-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20rpx;
}

.hot-city-item {
    padding: 20rpx;
    text-align: center;
    background: #f8f9fa;
    border-radius: 12rpx;
    cursor: pointer;
    transition: all 0.2s ease;
}

.hot-city-item:active {
    background: #e9ecef;
    transform: scale(0.98);
}

.hot-city-name {
    font-size: 28rpx;
    color: #333;
}

/*  */
.city-list-container {
    flex: 1;
    position: relative;
    overflow: hidden;
}

/*  */
.search-results {
    height: 100%;
    background-color: white;
}

.no-results {
    padding: 100rpx 20rpx;
    text-align: center;
}

.no-results-text {
    font-size: 28rpx;
    color: #999;
}

.search-result-list {
    padding: 0 20rpx;
}

.search-result-item {
    border-bottom: 1rpx solid #f0f0f0;
}

/*  */
.city-list {
    height: 100%;
    background-color: white;
}

.letter-section {
    margin-bottom: 20rpx;
}

.letter-header {
    background: #f8f9fa;
    padding: 15rpx 20rpx;
    border-bottom: 1rpx solid #e9ecef;
}

.letter-title {
    font-size: 24rpx;
    color: #666;
    font-weight: bold;
}

.city-group {
    background-color: white;
}

.city-item {
    padding: 30rpx 20rpx;
    border-bottom: 1rpx solid #f0f0f0;
    cursor: pointer;
    transition: background-color 0.2s ease;
}

.city-item:active {
    background-color: #f8f9fa;
}

.city-item:last-child {
    border-bottom: none;
}

.city-name {
    font-size: 32rpx;
    color: #333;
}

/*  */
.alphabet-index {
    position: fixed;
    right: 10rpx;
    top: calc(100vh - 480rpx);
    transform: translateY(-50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 999;
    background: rgba(0, 0, 0, 0.1);
    border-radius: 20rpx;
    padding: 10rpx 5rpx;
}

.index-letter {
    padding: 5rpx;
    width: 30rpx;
    height: 30rpx;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    transition: all 0.2s ease;
}

.index-letter.active {
    background-color: #565DF4;
}

.index-letter-text {
    font-size: 20rpx;
    color: #666;
    font-weight: bold;
}

.index-letter.active .index-letter-text {
    color: white;
}

/*  */
.letter-tip {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 120rpx;
    height: 120rpx;
    background: rgba(0, 0, 0, 0.8);
    border-radius: 12rpx;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
}

.letter-tip-text {
    font-size: 48rpx;
    color: white;
    font-weight: bold;
}
</style>
