<template>
  <view class="main-container">
    <!--  -->
    <view class="page-content">
      <!--  -->
      <home-page 
        v-if="currentTab === 0" 
        :class="{ 'page-active': currentTab === 0 }"
      />
      
      <!--  -->
      <order-page 
        v-if="currentTab === 1" 
        :class="{ 'page-active': currentTab === 1 }"
        @switch-tab="handleTabChange"
      />
      
      <!--  -->
      <profile-page 
        v-if="currentTab === 2" 
        :class="{ 'page-active': currentTab === 2 }"
      />
    </view>
    
    <!-- TabBar -->
    <custom-tabbar 
      :current-tab="currentTab" 
      @tab-change="handleTabChange"
    />
  </view>
</template>

<script>
import HomePage from './components/home-content.vue'
import OrderPage from './components/order-content.vue'
import ProfilePage from './components/profile-content.vue'
import CustomTabbar from '@/components/custom-tabbar/custom-tabbar.vue'

export default {
  name: 'MainContainer',
  components: {
    HomePage,
    OrderPage,
    ProfilePage,
    CustomTabbar
  },
  data() {
    return {
      currentTab: 0
    }
  },
  onLoad(options) {
    // 
    if (options.tab) {
      this.currentTab = parseInt(options.tab) || 0
    }
    
    console.log('tab:', this.currentTab)
  },
  onShow() {
    // TabBar
    uni.hideTabBar({
      animation: false
    })
  },
  methods: {
    handleTabChange(tabIndex) {
      if (this.currentTab === tabIndex) return
      
      console.log('tab:', tabIndex)
      this.currentTab = tabIndex
      
      // 
      uni.vibrateShort({
        type: 'light'
      })
      
      // 
      this.triggerPageAnimation(tabIndex)
    },
    
    triggerPageAnimation(tabIndex) {
      // 
      const pages = ['', '', '']
      console.log(`${pages[tabIndex]}`)
    }
  }
}
</script>

<style scoped>
.main-container {
  position: relative;
  width: 100%;
  min-height: 100vh;
  background-color: #f5f5f5;
  box-sizing: border-box;
}

.page-content {
  position: relative;
  width: 100%;
  min-height: calc(100vh - 120rpx);
  padding-bottom: 120rpx;
}

/*  */
.page-content > view {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 100%;
  transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  opacity: 0;
  transform: translateX(100%);
}

.page-content > view.page-active {
  opacity: 1;
  transform: translateX(0);
  position: relative;
}

/*  */
.page-content > view:not(.page-active) {
  pointer-events: none;
}

.page-content > view.page-active {
  pointer-events: auto;
}
</style>
